.card {
  position: fixed;
  top: 50%;
  left: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 480px;
  height: 480px;
  border-radius: 16px;
  margin-left: -240px;
  margin-top: -240px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
  color: rgba(255, 255, 255, 0.96);
}

.card:hover #logo {
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.48);
}

#logo {
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 10px;
  background: #000;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.48);
  -webkit-transition: box-shadow 0.4s;
  transition: box-shadow 0.4s;
  will-change: transform;
}

#logo:before {
  content: '>';
  position: absolute;
  top: 8px;
  left: 16px;
  font-size: 24px;
  font-weight: bolder;
}

#logo:after {
  content: '_';
  position: absolute;
  top: 8px;
  left: 36px;
  font-size: 24px;
  font-weight: bolder;
  -webkit-animation-name: underscore;
          animation-name: underscore;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: 2000ms;
          animation-duration: 2000ms;
  -webkit-animation-timing-function: step-start;
          animation-timing-function: step-start;
}

@-webkit-keyframes underscore {
  0% {
    color: #000;
  }
  50% {
    color: #fff;
  }
  100% {
    color: #000;
  }
}

@keyframes underscore {
  0% {
    color: #000;
  }
  50% {
    color: #fff;
  }
  100% {
    color: #000;
  }
}

h1 {
  line-height: 32px;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 2px;
}

.one {
  opacity: 0.4;
}

.pages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.pages a {
  padding: 0 4px;
  text-decoration: none;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
  color: rgba(255, 255, 255, 0.72);
}

a:hover {
  color: #999;
}

.home {
  position: relative;
  background-color: #1e282f;
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'avenir next', avenir, -apple-system, BlinkMacSystemFont, 'helvetica neue', helvetica,
    ubuntu, roboto, noto, 'segoe ui', arial, sans-serif;
  overflow: hidden;
}

